.nav {

  &__list {
    position: relative;
    display: flex;
  }

  &__item {
    margin-right: 64px;
    font-size: 12px;
    font-weight: bold;
    line-height: 0.92;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: block;
    line-height: $header-height;
    color: $secondary-color;
    @include hover(color, $primary-color, 0.2);

    //&--active {
    //  position: relative;
    //  color: $primary-color;
    //
    //  &::after {
    //    content: "";
    //    position: absolute;
    //    bottom: 0;
    //    left: 0;
    //    right: 0;
    //    height: 3px;
    //    background-color: $primary-color;
    //  }
    //}
  }

  &--active-element {
    display: block;
    position: absolute;
    top: 61px;
    left: 200px;
    bottom: 1px;
    color: $primary-color;
    height: 3px;
    background-color: $primary-color;
    transition: left 0.3s;
  }
}


